  transform-origin: center;
  animation: 1.4s ease-in-out infinite dash, 5.6s ease-in-out infinite colors;
}

@keyframes colors {
  0% {
    stroke: #4285f4;
  }

  25% {
    stroke: #de3e35;
  }

  50% {
    stroke: #f7c223;
  }

  75% {
    stroke: #1b9a59;
  }

  100% {
    stroke: #4285f4;
  }
}

@keyframes dash {
  0% {
    stroke-dashoffset: 187px;
  }

  50% {
    stroke-dashoffset: 46.75px;
    transform: rotate(135deg);
  }

  100% {
    stroke-dashoffset: 187px;
    transform: rotate(450deg);
  }
}

